"use client";

import { useState } from "react";
import { useCart } from "@/context/CartContext";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { ShoppingCart, Trash2, Edit2, CheckCircle2, Wallet, ArrowRight, User, Smartphone, Package } from "lucide-react";
import { toast } from "sonner";
import api from "@/lib/axios";
import { useRouter } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { cn } from "@/lib/utils";
import Link from "next/link";
import { AxiosError } from "axios";

export default function CartPage() {
    const { cartItems, removeFromCart, updateCartItem, clearCart, totalAmount } = useCart();
    const [editingId, setEditingId] = useState<string | null>(null);
    const [editPhone, setEditPhone] = useState("");
    const [loading, setLoading] = useState(false);
    const router = useRouter();

    const handleCheckout = async () => {
        if (cartItems.length === 0) return;
        setLoading(true);
        try {
            await api.post('/orders/bulk', {
                items: cartItems.map(item => ({
                    bundle_id: item.bundle_id,
                    recipient_phone: item.recipient_phone
                }))
            });
            toast.success("All orders placed successfully!");
            clearCart();
            router.push('/dashboard/orders');
        } catch (err) {
            const e = err as AxiosError<{ message: string }>;
            toast.error(e.response?.data?.message || "Checkout failed. Please check your balance.");
        } finally {
            setLoading(false);
        }
    };

    const startEditing = (id: string, currentPhone: string) => {
        setEditingId(id);
        setEditPhone(currentPhone);
    };

    const saveEdit = (id: string) => {
        updateCartItem(id, { recipient_phone: editPhone });
        setEditingId(null);
        toast.success("Phone number updated.");
    };

    if (cartItems.length === 0) {
        return (
            <div className="flex flex-col items-center justify-center min-h-[60vh] space-y-6 text-center animate-in fade-in duration-500">
                <div className="w-24 h-24 bg-gray-50 rounded-full flex items-center justify-center text-gray-300">
                    <ShoppingCart className="w-12 h-12" />
                </div>
                <div>
                    <h2 className="text-2xl font-bold text-gray-900">Your cart is empty</h2>
                    <p className="text-muted-foreground mt-2 max-w-xs mx-auto">Looks like you haven&apos;t added any data bundles yet. Start shopping to see them here!</p>
                </div>
                <Button asChild className="rounded-xl px-8 shadow-primary/20 shadow-lg">
                    <Link href="/dashboard/orders/new">Go Shopping</Link>
                </Button>
            </div>
        );
    }

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="space-y-8"
        >
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <div>
                    <h2 className="text-3xl font-bold tracking-tight text-primary">Shopping Cart</h2>
                    <p className="text-muted-foreground font-medium">Review your items and correct any errors before checkout.</p>
                </div>
                <Button variant="outline" onClick={clearCart} className="text-red-500 border-red-100 hover:bg-red-50 rounded-xl">
                    <Trash2 className="w-4 h-4 mr-2" />
                    Clear All
                </Button>
            </div>

            <div className="grid gap-8 lg:grid-cols-3">
                <div className="lg:col-span-2 space-y-4">
                    <AnimatePresence>
                        {cartItems.map((item) => (
                            <motion.div
                                key={item.id}
                                layout
                                initial={{ opacity: 0, scale: 0.95 }}
                                animate={{ opacity: 1, scale: 1 }}
                                exit={{ opacity: 0, scale: 0.95 }}
                                transition={{ duration: 0.2 }}
                            >
                                <Card className="sneat-card border-none shadow-sm hover:shadow-md transition-shadow group overflow-hidden">
                                    <div className="absolute left-0 top-0 w-1.5 h-full bg-primary opacity-20 group-hover:opacity-100 transition-opacity" />
                                    <CardContent className="p-6">
                                        <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-6">
                                            <div className="flex items-start gap-4">
                                                <div className="w-12 h-12 bg-primary/10 rounded-xl flex items-center justify-center text-primary shrink-0">
                                                    <Package className="w-6 h-6" />
                                                </div>
                                                <div className="space-y-1">
                                                    <h3 className="font-bold text-lg leading-none">{item.bundle_name}</h3>
                                                    <p className="text-[10px] font-bold uppercase tracking-wider text-muted-foreground flex items-center gap-2">
                                                        <span className="w-1.5 h-1.5 bg-primary rounded-full" />
                                                        {item.network}
                                                    </p>

                                                    <div className="mt-4 pt-4 border-t border-gray-50 flex items-center gap-4">
                                                        <div className="flex items-center gap-2">
                                                            <div className="w-8 h-8 bg-gray-50 rounded-lg flex items-center justify-center text-gray-400">
                                                                <Smartphone className="w-4 h-4" />
                                                            </div>
                                                            {editingId === item.id ? (
                                                                <div className="flex items-center gap-2">
                                                                    <Input
                                                                        value={editPhone}
                                                                        onChange={(e) => setEditPhone(e.target.value)}
                                                                        className="h-8 w-32 bg-white"
                                                                        autoFocus
                                                                    />
                                                                    <Button size="icon" variant="ghost" className="h-8 w-8 text-green-600" onClick={() => saveEdit(item.id)}>
                                                                        <CheckCircle2 className="w-5 h-5" />
                                                                    </Button>
                                                                </div>
                                                            ) : (
                                                                <div className="flex items-center gap-2">
                                                                    <span className="text-sm font-bold text-gray-700">{item.recipient_phone}</span>
                                                                    <button onClick={() => startEditing(item.id, item.recipient_phone)} className="text-muted-foreground hover:text-primary transition-colors">
                                                                        <Edit2 className="w-3.5 h-3.5" />
                                                                    </button>
                                                                </div>
                                                            )}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="flex flex-row sm:flex-col items-center sm:items-end justify-between sm:justify-center gap-4 border-t sm:border-t-0 pt-4 sm:pt-0 border-gray-50">
                                                <div className="text-right">
                                                    <p className="text-xs text-muted-foreground mb-1 uppercase tracking-widest font-bold">Price</p>
                                                    <p className="text-xl font-bold text-primary">GHS {Number(item.price).toFixed(2)}</p>
                                                </div>
                                                <Button
                                                    variant="ghost"
                                                    size="icon"
                                                    onClick={() => removeFromCart(item.id)}
                                                    className="text-gray-300 hover:text-red-500 hover:bg-red-50 rounded-lg"
                                                >
                                                    <Trash2 className="w-5 h-5" />
                                                </Button>
                                            </div>
                                        </div>
                                    </CardContent>
                                </Card>
                            </motion.div>
                        ))}
                    </AnimatePresence>
                </div>

                <div className="space-y-6">
                    <Card className="sneat-card border-none bg-primary text-primary-foreground overflow-hidden relative shadow-lg shadow-primary/20">
                        <div className="absolute right-[-20px] bottom-[-20px] opacity-10">
                            <ShoppingCart className="w-40 h-40" />
                        </div>
                        <CardHeader>
                            <CardTitle className="text-sm font-bold uppercase tracking-widest opacity-80">Order Summary</CardTitle>
                        </CardHeader>
                        <CardContent className="space-y-6">
                            <div className="space-y-2">
                                <div className="flex justify-between text-sm">
                                    <span className="opacity-80">Total Items</span>
                                    <span className="font-bold">{cartItems.length}</span>
                                </div>
                                <div className="flex justify-between items-end border-t border-white/20 pt-4">
                                    <span className="opacity-80 pb-1">Grand Total</span>
                                    <span className="text-4xl font-bold leading-none">GHS {Number(totalAmount).toFixed(2)}</span>
                                </div>
                            </div>

                            <Button
                                disabled={loading}
                                onClick={handleCheckout}
                                className="w-full h-14 bg-white text-primary hover:bg-white/90 border-none transition-all shadow-xl rounded-2xl font-bold text-lg group"
                            >
                                {loading ? "Processing..." : (
                                    <>
                                        Checkout Now
                                        <ArrowRight className="w-5 h-5 ml-2 group-hover:translate-x-1 transition-transform" />
                                    </>
                                )}
                            </Button>

                            <p className="text-[10px] text-center opacity-60 flex items-center justify-center gap-1.5">
                                <Wallet className="w-3 h-3" />
                                Combined deduction from your wallet balance
                            </p>
                        </CardContent>
                    </Card>

                    <Link href="/dashboard/orders/new" className="block">
                        <Card className="sneat-card border-dashed border-2 border-gray-200 bg-transparent text-gray-500 hover:border-primary/20 hover:bg-primary/5 transition-all group p-6 text-center cursor-pointer">
                            <div className="flex flex-col items-center gap-2">
                                <div className="w-10 h-10 bg-gray-100 rounded-full flex items-center justify-center group-hover:bg-primary/10 group-hover:text-primary transition-all">
                                    <ShoppingCart className="w-5 h-5" />
                                </div>
                                <p className="font-bold text-sm">Add More Items</p>
                                <p className="text-[10px]">Continue browsing data bundles</p>
                            </div>
                        </Card>
                    </Link>
                </div>
            </div>
        </motion.div>
    );
}
